import { useState, useEffect } from 'react';
import './App.css';
import loggo from './icons/loggo.svg';
import Post from './Components/Post.js';
import { db, auth } from './firebase';
import { makeStyles } from '@material-ui/core';
import { Button, Input } from '@material-ui/core';
import Modal from '@material-ui/core/Modal';

import ImageUpload from './ImageUpload';
// import InstagramEmbed from 'react-instagram-embed';


function getModalStyle() {
  const top = 50;
  const left = 50;

  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`,
  };
}

const useStyles = makeStyles((theme) => ({
  paper: {
    position: 'absolute',
    width: 400,
    backgroundColor: theme.palette.background.paper,
    border: '2px solid #000',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
}));




function App() {


  //setup state to get posts from someone
  const [posts, setPosts] = useState([]);
  const [open, setOpen] = useState(false);

  const [openSignIn, setOpenSignIn] = useState(false);


  const [username, setUsername] = useState();
  const [email, setEmail] = useState();
  const [password, setPassword] = useState()
  const [user, setUser] = useState(null);



  const classes = useStyles();
  const [modalStyle] = useState(getModalStyle);

  //useEffect-->runs a piece of code based on a specific condition

  useEffect(() => {
    db.collection('posts').orderBy('timestamp', 'desc').onSnapshot(snapshot => {

      setPosts(snapshot.docs.map(doc => ({
        id: doc.id,
        post: doc.data()
      })));

    })
  }, []);




  const signUp = (event) => {

    event.preventDefault();

    auth.createUserWithEmailAndPassword(email, password)
      .then((authUser) => {
        return authUser.user.updateProfile({
          displayName: username
        })

      })
      .catch((error) => alert(error.message))


    setOpen(false);

  }




  const signIn = (event) => {
    event.preventDefault();

    auth
      .signInWithEmailAndPassword(email, password)
      .catch((error) => alert(error.message))

    setOpenSignIn(false);
  }







  useEffect(() => {
    const unsubscribe = auth.onAuthStateChanged((authUser) => {
      if (authUser) {
        //user logged in...
        console.log(authUser);
        setUser(authUser);

      } else {
        //user logged out.
        setUser(null);
      }
    })



    return () => {
      //perform some clean actions....abs

      unsubscribe();
    }
  }, [user, username])







  return (
    <div className="App">







      <Modal
        open={open}
        onClose={() => setOpen(false)}
      >


        <div style={modalStyle} className={classes.paper}>


          <center>

            <img className="header_Image" src={loggo} alt="insta" />
            <form className="app_signUp">
              <Input
                placeholder="username"
                type="text"
                value={username}
                onChange={(e) => setUsername(e.target.value)}

              />

              <Input
                placeholder="email"
                type="text"
                value={email}
                onChange={(e) => setEmail(e.target.value)}

              />

              <Input
                placeholder="password"
                type="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}

              />

              <Button type="submit" onClick={signUp}>Sign Up</Button>

            </form>


          </center>

        </div>

      </Modal>



      {/*sign In*/}

      <Modal
        open={openSignIn}
        onClose={() => setOpenSignIn(false)}
      >


        <div style={modalStyle} className={classes.paper}>


          <center>

            <img className="header_Image" src={loggo} alt="insta" />
            <form className="app_signUp">


              <Input
                placeholder="email"
                type="text"
                value={email}
                onChange={(e) => setEmail(e.target.value)}

              />

              <Input
                placeholder="password"
                type="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}

              />

              <Button type="submit" onClick={signIn}>Sign In</Button>

            </form>


          </center>

        </div>

      </Modal>







      <div className="app_header">

        <img className="header_Image" src={loggo} alt="insta" />


        {user ? (
          <Button onClick={() => auth.signOut()}>Logout </Button>
        ) : (


            <div className="app_loginContainer">

              <Button onClick={() => setOpenSignIn(true)}>Sign In</Button>
              < Button onClick={() => setOpen(true)} >Sign Up</Button>
            </div>
          )
        }

      </div>






      <div className="app_posts">

        <div className="app_postsLeft">
          {
            posts.map(({ id, post }) => (
              <Post key={id} postId={id} username={post.username} user={user} caption={post.caption} imageUrl={post.imageUrl} />
            ))
          }
        </div>





      </div>









      {
        user?.displayName ? (

          <ImageUpload username={user.displayName} />

        ) : (
            <h3>Login to upload</h3>
          )
      }







    </div >
  );
}

export default App;


